import React, {useState} from 'react';
import axios from 'axios';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import Copyright from '../Atoms/Copyright';
let ip = '';
 if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
  // dev code
  ip = 'http://localhost:3000/signup';
} else {
  // production code
  console.log('add production code here');
  ip = 'https://horenvios.herokuapp.com/signup';

}


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function SignUp() {
  const classes = useStyles();
  const [data, setData] = useState({
    firstName: '',
    lastName: '',
    email : ''
});
const [alert, setAlert] = useState('empty');
const [alertMessage, setAlertMessage] = useState(<div></div>)
  const handleSubmit = (event) => {
    event.preventDefault()
    console.log('enviando datos...' + data.firstName + ' ' + data.lastName + ' ' + data.email + ' ' + data.password )
    const options = {
      headers: {credentials: 'include'}
    };
    axios.post(ip, {
      firstName: data.firstName,
      lastName: data.lastName,
      email : data.email,
      password: data.password
    }, options)
    .then(function (response) {
      console.log(response.data);
      setAlert('success');
      setAlertMessage(<Alert severity="success">{response.data}</Alert>);
      
    })
    .catch(function (error) {
      console.log(error);
      setAlert('error');
      setAlertMessage(<Alert severity="error">{error.response.data}</Alert>);
      
    });
  }
  const handleChange = (event) => {
    console.log(event.target.name)
    console.log(event.target.value)
   setData({
       ...data,
       [event.target.name] : event.target.value
   })
}
let formDisplay = null;
if(alert === 'empty' || alert === 'error'){
formDisplay =  <div className={classes.paper}>
<Avatar className={classes.avatar}>
  <LockOutlinedIcon />
</Avatar>
<Typography component="h1" variant="h5">
  Sign up
</Typography>
<form id='signup' className={classes.form} onSubmit={handleSubmit} noValidate>
  <Grid container spacing={2}>
    <Grid item xs={12} sm={6}>
      <TextField
        autoComplete="given-name"
        name="firstName"
        variant="outlined"
        required
        fullWidth
        id="firstName"
        label="First Name"
        autoFocus
        onChange={handleChange}
      />
    </Grid>
    <Grid item xs={12} sm={6}>
      <TextField
        autoComplete='family-name'
        variant="outlined"
        required
        fullWidth
        id="lastName"
        label="Last Name"
        name="lastName"
        onChange={handleChange}

      />
    </Grid>
    <Grid item xs={12}>
      <TextField
        autoComplete='email'
        variant="outlined"
        required
        fullWidth
        id="email"
        label="Email Address"
        name="email"
        onChange={handleChange}

      />
    </Grid>
    <Grid item xs={12}>
      <TextField
      autoComplete='new-password'
        variant="outlined"
        required
        fullWidth
        name="password"
        label="Password"
        type="password"
        id="password"
        onChange={handleChange}

      />
    </Grid>
    <Grid item xs={12}>
      <FormControlLabel
        control={<Checkbox value="allowExtraEmails" color="primary" />}
        label="I want to receive inspiration, marketing promotions and updates via email."
      />
    </Grid>
  </Grid>
  <Button
    type="submit"
    fullWidth
    variant="contained"
    color="primary"
    className={classes.submit}
   
  >
    Sign Up
  </Button>
  

  
</form>
</div>;
} else {
  formDisplay = null;
}
  return (
    <Grid container justify='center'>
    <Container component="main" maxWidth="xs">
      <CssBaseline />
     <Grid container justify="center">
            {formDisplay}
            <Grid item>
           
            {alertMessage}
            </Grid>
          </Grid>
          <Grid container justify="flex-end">
    <Grid item>
      <Link href="signin" variant="body2">
        Already have an account? Sign in
      </Link>
    </Grid>
  </Grid>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
    </Grid>
  );
}

export default SignUp;