import React from "react";
import {
  BrowserRouter,
  Switch
} from "react-router-dom";
import {connect} from 'react-redux';
import Home from './Pages/Home';
import SignUp from './Pages/Signup';
import SignIn from './Pages/SignIn';
import Dashboard from './PrivateRoutes/Dashboard';
import PrivateRoute from './PrivateRoutes/PrivateRoute';
import PublicRoute from './Pages/PublicRoute';
import AppBarUserInfo from './Molecules/AppBarUserInfo';
import Header from './Molecules/Header';
import { makeStyles } from '@material-ui/core/styles';
import './App.css';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column'
  },
  links : {
    color : 'white',
    textDecoration : 'none',
    "&:hover": {
      color: "gray",
  }
  }
}));
 function App(props) {
  const classes = useStyles();
  console.log(props)
  return (
    <BrowserRouter>
      <div className={classes.root}>
        {props.layout ? <AppBarUserInfo layout={props.layout} /> : null}
       
        <Header  layout={props.layout}   /> 
        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
       <Switch>
          <PublicRoute restricted={false} component={Home} path="/" exact />
          <PublicRoute restricted={true} component={SignIn} path="/signin"  exact  />
          <PublicRoute restricted={true} component={SignUp} path="/signup" exact />
          <PrivateRoute component={Dashboard} path="/dashboard" exact />
        </Switch>
      </div>
    </BrowserRouter>  
  );
}


function mapStateToProps(state) { 
  return {
     layout : state.changeLayout.hidePublicState
  };
};
export default connect(mapStateToProps)(App)
