import React from "react";
import {connect} from 'react-redux';
import { logout } from '../Utils/index';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from "react-router-dom";
import ButtonGroup from '@material-ui/core/ButtonGroup';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      [theme.breakpoints.down('xs')]: {
        flexDirection: 'column',
      }
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
    },
    links : {
      color : theme.palette.common.white,
      textDecoration : 'none'
  }
  }));
function Header(props) {
 // const [isItLogin, setIsLogin] = useState({isLogin: isLogin()})
    const classes = useStyles();
    const history = useHistory();

    const  handleLogout = () => {
      console.log('se dispara?')
      logout();
     // setIsLogin({ isLogin: false});
      history.push("/");
  }
let buttons = null;
console.log('props es', props)
if(props.layout){
buttons = <Button variant="contained" color="primary" onClick={() => handleLogout()}><Link className={classes.links} to="/">Logout</Link></Button>
} else {
  buttons = <ButtonGroup className={classes.root} variant="contained" color="primary" aria-label="contained primary button group">
  <Button><Link className={classes.links} to="/">Home</Link></Button>
  <Button><Link className={classes.links}  to="/signup">SignUp</Link></Button>
  <Button><Link className={classes.links}   to="/signin">SignIn</Link></Button>
  </ButtonGroup>;
}

    return (
        <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Horenvios
            </Typography>
            <nav >
          <ul>
        {buttons}
          </ul>
        </nav>
          </Toolbar>
        </AppBar>
      </div>
    )
  }
  
  function mapStateToProps(state) { 
    return {
       userInfo : state.setUserInfo.userInfo
    };
  };
  export default connect(mapStateToProps)(Header)
  
