import React, {useState} from 'react';
import axios from 'axios';
import store from '../Redux/store';
import {userInfo} from '../Redux/actions';
import { useHistory } from 'react-router-dom';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Alert from '@material-ui/lab/Alert';
import  {login} from '../Utils/index';
import Copyright from '../Atoms/Copyright';
let ip = '';
 if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
  // dev code
  ip = 'http://localhost:3000/signin';
} else {
  // production code
  console.log('add production code here');
  ip = 'https://horenvios.herokuapp.com/signin';

}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

 function SignIn(props) {
  const classes = useStyles();
  const history = useHistory();

 // const [alert, setAlert] = useState('empty');
const [alertMessage, setAlertMessage] = useState(<div></div>) 
  const [data, setData] = useState({
    email : '',
    password : ''
});
  const handleSubmit = (event) => {
    event.preventDefault()
    const options = {
      headers: {credentials: 'include'}
    };

    axios.post(ip, {
      email : data.email,
      password: data.password
    }, options)
    .then(function (response) {
      console.log(response.data);
      store.dispatch(userInfo(response.data))
      //store.dispatch(changeLayout(true))
      login();
      history.push("/dashboard");
     // setAlert('success');
     // setAlertMessage(<Alert severity="success">{response.data}</Alert>);
      
    })
    .catch(function (error) {
     // debugger;
    //  console.log(error.response.data);
     // setAlert('error');
      setAlertMessage(<Alert severity="error">{error.response.data}</Alert>);
      
    });
  }
  const handleChange = (event) => {
    console.log(event.target.name)
    console.log(event.target.value)
   setData({
       ...data,
       [event.target.name] : event.target.value
   })
}
  return (
    <Grid container justify='center'>
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={handleChange}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={handleChange}
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="signup" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Grid container justify="center">
            {/*formDisplay*/}
            <Grid item>
           
            {alertMessage}
            </Grid>
          </Grid>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
    </Grid>
  );
}

export default SignIn;