import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  root: {
    color : theme.palette.primary.contrastText,
    [theme.breakpoints.down('xs')]: {
      fontSize: 'xx-small'
    }
  },
  secondary : {
    color :
    theme.palette.secondary.light,
    [theme.breakpoints.down('xs')]: {
      fontSize: 'xx-small'
    }
  },
 

}));

export default function UserInfo(props) {
  const classes = useStyles();

  return (      
                <Grid alignItems='center' container direction='column' justify='center' >
                <Typography variant='subtitle1' className={classes.root}>{props.Title}:</Typography> 
                <Typography variant='subtitle1' className={classes.secondary}>{props.Data}</Typography>  
                </Grid> 
  );
}
